@import "../../adaptive-tools";


.skills{
    max-width: 100%;
    &__content{
        display: flex;
        justify-content: space-around;
        ul{
            list-style: none;
            li{
                display: flex;
                line-height: 200%;
                img{
                    height: vh(22px);
                    width: vw(22px);
                }
            }
        }
    }
}


@media screen and (max-width: $mobile-screen-vw) {
    .skills{
        &__content{
            flex-direction: column;
            align-items: center;
            ul{
                li{
                    img{
                        height: mobile_vh(22px);
                        width: mobile_vw(22px);
                    }
                }
            }
        }
    }

}