@import "../../adaptive-tools";


.header{
    position: fixed;
    height: vh(50px);
    width: 100%;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    z-index: 100;
    &.light{
        background: rgb(152, 61, 238);
        color: white;
    }
    &.dark{
        background: rgb(44, 43, 44);
        color: white;
    }
}

.theme__toggle{
    width: vw(80px);
    height: vh(32px);
    margin: vh(10px) vw(80px);
    padding: 0;
    border-radius: 30px;
    display: flex;
    align-items: center;
    cursor: pointer;
    position: relative;
    border: 2px solid gray;
    &:before{
        content: '';
        width: vw(30px);
        height: vh(29px);
        background-color: white;
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    &.light{
        &:before{
            transform: translateX(2%);
            transform: translateY(-2%);
            background-color: black;
            transition-duration: 1s;
        }
        background-color: white;
    }
    &.dark{
        &:before{
            transform: translateX(153%);
            height: vh(28px);
            background-color: white;
            transition-duration: 1s;
        }
        background-color: rgba(0, 0, 0, 0.897);
    }
}
.language__toggle{
    padding: vh(5px) vw(10px);
    margin-right: vw(32px);
    border-radius: 6px;
    width: vw(60px);
    cursor: pointer;
    &.light{
        color: black;
        background: rgb(228, 225, 225) !important;
    }
    &.dark{
        color: white;
        background: black !important;
        box-shadow: inset 0 0 5px white;
    }
}


@media screen and (max-width: $mobile-screen-vw) {
    .header{
        height: mobile_vh(50px);
    }
    .theme__toggle{
        width: mobile_vw(80px);
        height: mobile_vh(32px);
        margin: mobile_vh(10px) mobile_vw(80px);
        &:before{
            width: mobile_vw(30px);
            height: mobile_vh(29px);
        }
        &.dark{
            &:before{
                height: mobile_vh(28px);
            }
        }
    }
    .language__toggle{
        padding: mobile_vh(5px) mobile_vw(10px);
        margin-right: mobile_vw(32px);
        width: mobile_vw(60px);
    }
}